import {
	productCateList,
	getProduct
} from '@/api/homeApi.js';
import {
	mapMutations
} from 'vuex';

let currTabId;

export default {
	data() {
		return {
			tabActive: 0,
			screenActive: 0,
			screenDirection: true,
			tabsData: [],
			goodsData: [],
			// params: {
			// 	"keyword": "",
			// 	"productCategoryId": 0
			// },
			pageSize: 10,
			pageNum: 1
		}
	},
	onLoad(option) {
		let id = option.id;
		let tabId = option.tabId - 0;
		productCateList(id).then(res => {
			if (res.code == 200) {
				this.tabsData = res.data;
				if (!tabId) {
					tabId = res.data[0].id;
				}
				this.tabActive = tabId;
				this.getGoodsData(tabId);
			}
		})
	},
	onReachBottom(e){
		console.log(e);
	},
	methods: {
		...mapMutations(['setRouterWait']),
		getGoodsData(id) {//获取商品列表
			let params = {
				keyword:"",
				productCategoryId:id
			} 
			if(this.screenActive === 0){//最新
				params.sale = false;
				params.price = false;
			}else if(this.screenActive === 1){//销售
				params.sale = this.screenDirection;
			}else{//价格
				params.price = this.screenDirection;
			}
			getProduct({
					...params
				},
				this.pageSize,
				this.pageNum).then(res => {
				if (res.code == 200) {
					this.goodsData = res.data.list;
				}
			})
		},
		screen(index) { //条件筛选
		
			if (this.screenActive != index) {
				this.screenActive = index;
				this.screenDirection = true;
			} else {
				this.screenDirection = !this.screenDirection;
			}
			this.getGoodsData(this.tabActive);
		},
		tabChange(e) {//类别选择
			this.tabActive = e.detail.name;
			this.getGoodsData(this.tabActive);
		},
		shopCart(item){
			
			this.setRouterWait({
				routerPath:'../cart/cart',
				routerType:'switchTab',
				routerData:item
			});
			
			this.$router.switchTab({
				url: `../cart/cart`
			});
			
		}
	}
}
